                                                    >
                                                        <a target="_blank" :href="getLink('quest', item.questId)">
                                                            [{{ item.questName }}]</a
                                                        >
                                                    </div>
                                                </div>
                                            </template>
                                        </div>
                                        <span>{{ getOrigin(book) }}</span>
                                    </el-tooltip>
                                </div>
                                <div v-else class="u-item">书籍来源：{{ getOrigin(book) }}</div>
                                <div class="u-item">所属套书：{{ book.BookName }}</div>
                                <div v-if="book.AchievementID" class="u-item">
