                                    </div>
                                    <div class="u-item">
                                        <span>消耗精力：</span>
                                        <span>{{ book.copy.CostVigor }}点</span>
                                    </div>
                                    <div v-if="book.copyList.length" class="u-item">
                                        <span>消耗材料：</span>
                                        <item-icon
                                            v-for="meterial in book.copyList"
                                            :key="meterial.item_id"
